<template>
    <div id="test">
        {{ info }}
    </div>
</template>
<script>
    
    export default {
        data() {
            return {
                info: null
            };
        },
        mounted() {
            this.$axios
                .get('jsonapi/product')
                .then(response => (this.info = response));
        }
    };
</script>
